@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
.outer-container {
  width: 400px;
  height: 600px;
  margin: auto;
}

.calculator {
  background-color: bisque;
  width: 100%;
  height: 100%;
  border-radius: 5%;
}
.display__container {
  width: 90%;
  height: 21%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.display {
  width: 100%;
  height: 55%;
  margin: auto;
  background-color: #BBB;
  color: #222;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
}

.hidden {
  color: #BBB;
}

.display-mode {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 70%;
  align-self: flex-start;
  justify-self: flex-start;
  padding-left: 10%;
  height: 25%;
}

.display-text {
  font-family: 'VT323', monospace;
  text-transform: uppercase;
  text-align: right;
  font-size: 280%;
  margin: 0;
  padding-right: 10px;
  margin-bottom: 1%;
}
.keypad-container {
  /* background-color: cadetblue; */
  width: 90%;
  margin: auto;
  font-family: Arial, Helvetica, sans-serif;
}

.logo {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 400%;
}


.keys {
  display: flex;
  flex-flow: row wrap;
}

.key-container {
  
  /* flex-grow: 1; */
  height: 90px;
  width: 25%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.key {
  width: 90%;
  height: 90%;
  background-color: #111;
  color: #eee;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5%;
  cursor: pointer;
  -webkit-animation-duration: .05s;
          animation-duration: .05s;

  font-size: 200%;
  font-weight: bolder;
}

.key p {
  /* background-color: crimson; */
  display: flex;
  justify-content: center;
  align-items: center;
}

.op-font-size {
  font-size: 70%;
}

@-webkit-keyframes bounceIn {
  0% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(1);
  }

}

@keyframes bounceIn {
  0% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(1);
  }

}

.bounceIn {
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-name: bounceIn;
          animation-name: bounceIn;
}
